import React, { useEffect, useRef, useState } from 'react';
import {NavLink} from 'react-router-dom';
import styles from './style.module.sass';

export interface Link {
    link : string;
    logo : string;
    name : string;
}

interface Props {
    links: Array<Link>
}

const MobileNav : React.FC<Props> = ({links}) => {
    const [offset,setOffset] = useState(0);
    let lastScrollTop = 0;


    const onScroll  = (e: Event)=>{
        const st = window.pageYOffset || document.documentElement.scrollTop;
        if (st > lastScrollTop){
            setOffset(offset => {
                const nestStep = offset + st-lastScrollTop;
                return nestStep < 66 ?  nestStep : 67
            });
        } else {
            setOffset(offset => {
                const nestStep = offset + st-lastScrollTop;
                return nestStep > 0 ? nestStep : 0;
            });
        }
        lastScrollTop = st <= 0 ? 0 : st; 
    }

    useEffect(()=>{
        window.onscroll = onScroll;
        return  () => {
            window.onscroll = null;
        }
    },[])

    return (
        <nav style={{transform: `translateY(${offset}px)`}} className={`${styles.wrapper}`}>
            {(()=>{
                return links.map(({link,logo,name},key)=> (
                    <NavLink key={key} className={styles.link} activeClassName={styles.active} exact={link === '/'}  to={link}>
                        <span>{name}</span>
                        <svg width={20} height={20}>
                            <use xlinkHref={logo} />
                        </svg>
                    </NavLink>
                ))
            })()}
        </nav>
    )
}

export default MobileNav;